html {
  overflow-x: hidden;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#c9d6ff), to(#e2e2e2));
  background: -webkit-linear-gradient(left, #c9d6ff, #e2e2e2);
  background: -o-linear-gradient(left, #c9d6ff, #e2e2e2);
  background: linear-gradient(to right, #c9d6ff, #e2e2e2);
}

.container {
  width: 1100px;
  margin: 20px auto;
  padding-top: 20px;
}

@media (max-width: 1120px) {
  .container {
    width: 880px;
    margin: 20px auto;
  }
}

@media (max-width: 900px) {
  .container {
    width: 660px;
    margin: 20px auto;
  }
}

@media (max-width: 700px) {
  .container {
    width: 480px;
    margin: 20px auto;
  }
}

@media (max-width: 500px) {
  .container {
    width: 440px;
    margin: 20px auto;
  }
}

@media (max-width: 460px) {
  .container {
    width: 400px;
    margin: 20px auto;
  }
}

@media (max-width: 420px) {
  .container {
    width: 380px;
    margin: 20px auto;
  }
}

@media (max-width: 400px) {
  .container {
    width: 330px;
    margin: 20px auto;
  }
}

@media (max-width: 350px) {
  .container {
    width: 300px;
    margin: 20px auto;
  }
}

.clearfix {
  zoom: 1;
}
.clearfix:after {
  content: " ";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden;
}

@media (min-width: 800px) {
  .mobileOnly {
    display: none;
  }
}

@media (max-width: 800px) {
  .desktopOnly {
    display: none;
  }
}



