.ant-carousel .slick-slide {
  text-align: center;
  height: 70vh;
  line-height: 160px;
  background: #364e799f;
  overflow: hidden;
  margin-top: 20px;
  border-radius: 20px;
}

.NewsSlider__HorizontalContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.NewsSlider__ImageContainer {
  width: 100%;
  margin: 0;
  padding: 0;
}

.NewsSlider__Image {
  margin: 0 auto;
  height: 50vh;
  width: 85%;
  margin-top: 30px;
  -webkit-box-shadow: 15px 25px 58px 19px rgba(0, 0, 0, 0.75);
          box-shadow: 15px 25px 58px 19px rgba(0, 0, 0, 0.75);
  margin-bottom: 20px;
}

.NewsSlider__Arrow {
  color: #fff;
  cursor: pointer;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

.NewsSlider__Title {
  color: #fff;
  font-size: 25px;
  margin-left: 20px;
  margin-right: 20px;
}

.NewsSlider__Date {
  margin: 0;
  color: black;
  font-weight: bold;
}

@media (max-width: 700px) {
  .NewsSlider__Title {
    font-size: 15px;
  }
}

@media (max-height: 650px) {
  .ant-carousel .slick-slide {
    height: 80vh;
  }
}



